* {
    box-sizing: border-box;
}

body {
    font-family: 'Microsoft JhengHei';
}

.wrap {
    width: 100%;
    margin: 0 auto;
}

section {
    padding-top: 25px;
    padding-bottom: 25px;
}

.course-content .slider {
    margin: 20px auto;
}

.slick-slide {
    margin: 12px;
    width: 330px;
}

@media (max-width: 768px) {
    .slick-slide {
        width: 350px;
    }
}

@media (max-width: 568px) {
    .slick-slide {
        width: 220px;
        margin: 12px 6px;
    }
}

.slick-slide img {
    width: 100%;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.25);
}

.slick-slide img:hover {
    transition: all 0.5s;
    opacity: 0.8;
}

.slick-prev:before,
.slick-next:before {
    color: black;
    font-size: 25px;
}

@media (max-width: 768px) {
    .slick-prev:before,
    .slick-next:before {
        display: none;
    }
}

.course-search {
    margin-right: 10px;
}

.course-search > span {
    display: block;
    font-size: 18px;
}

.course-search select {
    border: 1px solid gray;
    border-radius: 5px;
    padding: 5px 30px;
    margin-right: 10px;
    cursor: pointer;
}

@media (max-width: 568px) {
    .course-search select {
        display: block;
        width: 100%;
        margin-bottom: 10px;
    }
}

.course-search select option {
    font-size: 16px;
}

.btn-custom {
    background-color: #37761D;
    border: 1px solid #37761D;
    color: #fff;
    padding-left: 10px;
    padding-right: 10px;
}

.course-box {
    padding-bottom: 80px;
}

.course-box h2 {
    display: inline;
    font-size: 36px;
    border-bottom: 5px solid #f05f40;
    padding-bottom: 5px;
}

.course-box > ul {
    list-style: none;
    padding: 0;
}

@media (max-width: 568px) {
    .course-box > ul .col-xs-6 {
        width: 100%;
    }
}

.my-course-card {
    margin-top: 20px;
    padding-top: 1px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
    background-color: rgba(255, 255, 255, 0.95);
}

.my-course-card h3 {
    font-size: 32px;
    text-align: center;
    padding-top: 5px;
    padding-bottom: 10px;
    text-shadow: 2px 2px 5px gray;
}

.my-course-card h3 a {
    color: #37761d;
}

@media (max-width: 768px) {
    .my-course-card h3 {
        font-size: 26px;
    }
}

.my-course-card center a {
    margin-top: 10px;
    margin-bottom: 30px;
    margin-right: 5px;
}

.my-course-card center .btn-pay {
    background-color: #37761D;
}

.my-course-card center .btn-pay:hover {
    background-color: #4a9f27;
    border: 1px solid #4a9f27;
}

.card-inform {
    border-radius: 30px;
    min-height: 250px;
    margin-left: 15px;
    margin-right: 15px;
    position: relative;
}

.card-inform .card-status {
    display: inline-block;
    background-color: #4da529;
    padding: 5px 10px;
    border-radius: 10px;
    position: absolute;
    right: 20px;
    bottom: 20px;
}

.card-inform .card-status > a {
    font-size: 24px;
    font-weight: bold;
    color: #fff;
}

.first-lesson {
    background-image: url(../img/uian.jpg);
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
}

.second-lesson {
    background-image: url(../img/hankcoach.jpg);
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
}

.third-lesson {
    background-image: url(../img/wenyen2.jpg);
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
}

.forth-lesson {
    background-image: url(../img/coachS_Mona.jpg);
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
}

.course-list {
    list-style: none;
    margin-top: 5px;
    padding-right: 30px;
}

.course-list > li > ul > li {
    display: inline-block;
    padding-top: 10px;
    padding-bottom: 5px;
    border-bottom: 1px solid #D5D8DC;
    font-size: 14px;
}

.course-detail {
    list-style: none;
    font-size: 16px;
}

.course-detail li {
    padding: 3px 0;
}

.course-default {
    line-height: 1em;
    padding-bottom: 20px;
}

.service-content .panel-title {
    font-weight: bold;
}

.service-content .panel-body h3 {
    font-size: 18px;
    display: inline;
    border-bottom: 2px solid #37761d;
    padding-bottom: 2px;
}

.service-content .panel-body > span {
    display: block;
    padding-top: 10px;
    color: #37761d;
    font-weight: bold;
}

.service-content .panel-body ul {
    margin-bottom: 20px;
    padding-top: 10px;
    line-height: 1.3em;
}

.service-content .panel-body ul li {
    margin-bottom: 5px;
}

.date-content h2 {
    margin: 0;
    padding-left: 5px;
    padding-bottom: 20px;
    letter-spacing: 5px;
}

.date-content ul {
    list-style: none;
    margin-top: -10px;
    padding: 0;
    margin-left: 10px;
    padding-bottom: 80px;
}

.date-content ul i {
    display: none;
}

@media (max-width: 768px) {
    .date-content ul i {
        display: inline-block;
        padding-top: 18px;
        padding-right: 15px;
    }
}

@media (max-width: 768px) {
    .date-content ul span {
        display: none;
    }
}


/* arrow for step process */

.arrow {
    float: left;
    width: 22%;
    height: 50px;
    background-color: rgba(55, 118, 29, 0.3);
    border-radius: 5px 5px 0 0;
    position: relative;
    margin-right: 18px;
    margin-bottom: 20px;
    text-align: center;
    line-height: 45px;
}

.arrow > a {
    display: block;
    text-decoration: none;
    color: #37761D;
    font-size: 14px;
    font-weight: bold;
    padding: 0 12px;
}

.arrow > a > i {
    padding-left: 15px;
}

@media (max-width: 990px) {
    .arrow {
        width: 21%;
    }
}

@media (max-width: 512px) {
    .arrow {
        width: 18%;
    }
}

@media (max-width: 414px) {
    .arrow > a {
        padding: 0;
    }
}

.arrow:after {
    content: '';
    position: absolute;
    right: -22px;
    top: 30%;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-left: 12px solid rgba(55, 118, 29, 0.3);
}

.arrow-active {
    background-color: rgba(55, 118, 29, 0.7);
}

.arrow-active > a {
    color: #fff;
}

.arrow-active:after {
    border-left: 12px solid rgba(55, 118, 29, 0.7);
}

.arrow-none:after {
    border: 0;
}

.cursor-not-allow {
    cursor: not-allowed;
}

.cursor-initial {
    cursor: default;
}

.cursor-pointer {
    cursor: pointer;
}

.target-select > h3 {
    font-size: 22px;
    font-weight: bold;
}

.target-select .form-qa {
    padding-top: 10px;
}

.target-select .form-qa label {
    font-size: 15px;
}

.target-select .target-time {
    display: inline-block;
    padding: 10px 30px;
    border: 1px solid gray;
    border-radius: 5px;
    position: relative;
}

.target-select .next-step {
    width: 97%;
    background-color: #37761d;
    border: 1px solid #37761d;
    border-radius: 10px;
    padding: 5px 10px;
    margin-top: 20px;
    color: #fff;
    font-weight: bold;
}

.target-select .next-step:hover {
    background-color: #459725;
    border: 1px solid #459725;
}

.target-select .btn-submit {
    margin-top: 50px;
}

.target-select .alert {
    width: 97%;
}

.target-select .panel {
    width: 97%;
}

.ui-widget-header {
    border: 1px solid #37761D;
    background-color: #37761D !important;
}
